<template>
	<!--  -->
	<!--  -->
	<div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="0">
		<section class="recommend">
			<header class="recommend_header">
				<span>:</span>
				<img src="static/img/close.svg" alt="">
			</header>
			<hr>
			<div class="recommend_content">
				<img :src="follow.header_img" alt="" class="header_pic">
				<div>
					<span>{{follow.host_num}}[]</span><br>
					<span>{{follow.activity_host}}</span>
				</div>
				<button @click="takeFollow" class="follow">{{follow.followState ? '':''}}</button>
				<!--  -->
			</div>
		</section>
		<hr class="for_shadow">
		<section>
			<ul>
				<li v-for="(item, index) in trendsList" :key="index" class="people_trends">
					<section class="people_trends_header">
						<router-link to="#">
							<img :src="item.header_img + '?imageView2/2/w/50/h/50'" alt="" class="header_pic">
						</router-link>
					</section>
					<section>
						<router-link to="#">
							<div class="people_trends_time">
								<span>
									<span class="people_trends_nick">{{item.name}}</span><br>
									<span class="people_trends_timer">{{item.time}}</span>
								</span>
								<img src="static/img/down.svg" alt="down">
							</div>
							<p>{{item.description}}</p>
							<section class="peopel_trends_contend_pic">
								<img :src="item.img[0] + '?imageView2/5/w/100/h/150'" alt="">
								<img :src="item.img[1] + '?imageView2/5/w/100/h/150'" alt="">
								<img :src="item.img[2] + '?imageView2/5/w/100/h/150'" alt="">
							</section>
						</router-link>
						<div class="people_trends_commend">
							<span>:{{item.circle_name}}</span>
							<div>
								<span><img src="static/img/heart.svg" alt="">{{item.likes}}</span>
								&nbsp;
								<span><img src="static/img/comment.svg" alt="">{{item.talk_num}}</span>
								&nbsp;
							</div>
							
						</div>
					</section>
				</li>
			</ul>
		</section>
	</div>
</template>
<script>
	import api from "../../fetch/circle"
	export default{
		name:'circleTrends',
		data(){
			return {
				finish: false,
				busy: false,
				pageIndex: 0,
				follow: [],
				trendsList: [],
			}
		},
		created(){

		},
		mounted(){
			this.init();
		},
		
		components:{

		},
		computed: {

		},
		methods:{
			init(){
				setTimeout(() => {
					this.getFollow();
				},300)
				
			},
			getFollow() {
				let data = {
					num: 1
				}
				api.getRecommendUser(data)
					.then( res => {
						this.follow = res.message[0];
					})
					.catch( err => {
						console.log(err)
					})
			},
			getDataList() {
				let data = {
					pageIndex: this.pageIndex,
					pageSize: 10,
				}
				api.getCircleTrends(data)
					.then( res => {
						let resData = res.message;
						if (resData.length == 0) {
							this.finish = true;
						}
						for(let item in resData){
							let temTime = resData[item].time;
							temTime = (new Date()) - (new Date(temTime));
							// 
							temTime = Math.round(temTime/1000/60/60*10)/10;
							resData[item].time = temTime;
						}
						this.trendsList.push(...resData);
						this.busy = false;
					})
					.catch( err => {
						console.log(err)
					})
			},
			takeFollow() {
				this.follow.followState = true;
				this.getFollow();
			},

			loadMore(){
				if (!this.finish) {
					this.pageIndex += 1;
					this.busy = true;
					this.getDataList();
				}else{
					return;
				}
			},
		}
	}
</script>
<style lang="scss" scoped>
.recommend {
	width: 96vw;
	margin: auto 2vw;
}
.for_shadow {
	// border: 1px solid gray;
	border-bottom: 1vh solid #e6e6e6;
}
.recommend_header {
	display: flex;
	justify-content: space-between;
}
.recommend_content {
	display: flex;
	justify-content: flex-start;
	img {
		width: 13vw;
		height: 13vw;
		margin: {
			right: 2vw;
		}
	}
	.follow {
		background-color: white;
		width: 20vw;
		height: 8vw;
		border: 1px solid red;
		border-radius: 20px;
		margin-left: 20vw;
	}
}
.people_trends {
	display: flex;
	width: 90vw;
	margin: 2vw;
	.people_trends_header {
		margin-right: 2vw;
	}
	.peopel_trends_contend_pic {
		display: flex;
		// justify-content: space-between;
		img {
			margin: 10px 1px;
			width: 27vw;
		}
		
	}
	.people_trends_time {
		display: flex;
		justify-content: space-between;
		margin-bottom: 1.5vh;
		.people_trends_nick {
			font-size: .16rem;
			color: gray;
		}
		.people_trends_timer {
			@extend .people_trends_nick;
			font-size: .12rem;
		}
	}
	.people_trends_commend {
		display: flex;
		justify-content: space-between;
		border-bottom: 1px solid #ccc;
		padding-bottom: .2rem;
		margin-bottom: .2rem;
		
		span {
			color: gray;
		}
		
	}
}
</style>



